/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.grid {
  display: flex;
  justify-content: center;
}

.topbar {
  background-color: black;
  color: white;
  height: 30px;
  font-size: 1rem;
  display: inline-block;
  width: 100vw;
  padding-left: 50px;
  padding-top: 20px;
  margin-top: -7px;
}

.site {
  background-color: white;
}

.Footer {
  background-color: rgb(0, 0, 0);
  color: white;
  height: 200px;
  width: 100vw;
  margin-left: -20px;
  padding-left: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 50px solid black;
  box-sizing: content-box;
}

.FooterLogo {
  height: 100px;
  background-color: black;
  margin-top: 50px;
  margin-left: -50px;
  z-index: 6;
}

html {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
