/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.grid {
  display: flex;
  justify-content: center;
}

.topbar {
  background-color: black;
  color: white;
  height: 30px;
  font-size: 1rem;
  display: inline-block;
  width: 100vw;
  padding-left: 50px;
  padding-top: 20px;
  margin-top: -7px;
}

.site {
  background-color: white;
}

.Footer {
  background-color: rgb(0, 0, 0);
  color: white;
  height: 200px;
  width: 100vw;
  margin-left: -20px;
  padding-left: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 50px solid black;
  box-sizing: content-box;
}

.FooterLogo {
  height: 100px;
  background-color: black;
  margin-top: 50px;
  margin-left: -50px;
  z-index: 6;
}

html {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.Image {
  object-fit: cover;
  width: 200px;
  height: 200px;
}

.fade {
  width: 200px;
  height: 200px;
  position: absolute;
  background-color: black;
  opacity: 0.5;
}

/* .fade:hover { */
/* opacity: 0.2; */
/* } */

.ParkName {
  float: center;
  text-align: center;
  font-size: 15px;
  color: white;
  position: absolute;
  margin-top: 110px;
  margin-left: 50px;
  z-index: +2;
  width: 100px;
}

.parkcomponent-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  margin: 10px 20px 20px 20px;
}

.AllParks {
  display: flex;
  flex-flow: wrap;
  width: 100vw;
  box-sizing: border-box;
  padding: 20px 15px 10px 15px;
  justify-content: center;
  align-items: center;
}

.ParkPage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: darkblue;
  max-width: 100vw;
}

.ParkMainImageBox {
  display: flex;
  flex-flow: wrap;
  box-sizing: border-box;
  height: 300px;
  max-width: 100vw;
}

.ParkMainImage {
  object-fit: cover;
  width: 100vw;
  height: 300px;
  object-position: top;
  max-width: 100vw;
}

.ParkNameBox {
  text-align: left;
  padding-left: 45px;
  margin-top: -20px;
}

.ParkNameHeader {
  text-align: left;
  margin-top: -40px;
  font-size: 30px;
  width: 600px;
  color: white;
}

.ParkDescriptionBox {
  color: white;
  background-color: black;
  padding: 30px 50px 30px 8px;
  font-size: 11px;
  width: 100vw;
}

.ParkDescription {
  margin-top: -10px;
  margin-left: 37px;
  margin-right: 50px;
  line-height: 1.5em;
}

.AddressBox {
  margin-left: 40px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.AddressHeader {
  font-size: 13px;
  margin-bottom: 8px;
}
.AddressInfo {
  font-size: 10px;
  line-height: 1.5;
}
.DirectionsBox {
  font-size: 9px;
  margin-left: 40px;
  margin-right: 50px;
  line-height: 1.25em;
}

.DirectionsHeader {
  font-size: 12px;
  margin-bottom: 5px;
}

.ReadMoreButton {
  margin-top: 15px;
  font-size: 8px;
  background-color: black;
  color: white;
  width: 75px;
  text-align: center;
  height: 15px;
  padding-top: 3px;
}

.ParkImageContainer {
  margin-top: 5px;
  display: flex;
  flex-flow: wrap;
  flex-direction: column;
  width: 100vw;
  box-sizing: border-box;
  padding: 10px;
  margin-left: 30px;
  font-size: 12px;
}

.ImageBox {
  object-fit: cover;
  width: 150px;
  height: 150px;
  margin: 10px 14px 5px 0px;
  border: 0.5px solid black;
}

.HomeLink {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 40px;
  font-size: 11px;
}


